<template>
  <div class="recommend">
    <div v-if="this.active==0" id='tt'>
      <van-sticky id="test">
        <a
          href="http://mm.10086.mmarket.com/mm-download/dist/?version=5&fromWx=false"
          class="headerImg"
        >
          <img src="../../asset/heard.png" class="background-img" />
          <a href="http://ota.mmarket.com:38080/rs/res1/mmclient/MM_online_channel_5210543595.apk">
            <img src="../../asset/download.png" class="download-img" />
          </a>
        </a>
      </van-sticky>
      <div id="recom-tab">
        <van-tabs v-model="active" title-active-color="#3386e2" color="#3386e2">
          <van-tab title="推荐" :to="{ path: '/main/recom'}"></van-tab>
          <van-tab title="软件" :to="{ path: '/main/app'}"></van-tab>
          <van-tab title="游戏" :to="{ path: '/main/GameRecom'}"></van-tab>
          <van-tab title="榜单" :to="{ path: '/main/appList'}"></van-tab>
        </van-tabs>
      </div>
    </div>
    <div v-else>
      <van-sticky id="recom-tab">
        <van-tabs v-model="active" title-active-color="#3386e2" color="#3386e2" v-if="active!=3">
          <van-tab title="推荐" :to="{ path: '/main/recom'}"></van-tab>
          <van-tab title="软件" :to="{ path: '/main/app'}"></van-tab>
          <van-tab title="游戏" :to="{ path: '/main/GameRecom'}"></van-tab>
          <van-tab title="榜单" :to="{ path: '/main/appList'}"></van-tab>
        </van-tabs>
      </van-sticky>
    </div>

    <van-search
      v-model="value"
      placeholder="请输入搜索关键词"
      show-action
      shape="round"
      @search="onSearch"
      @focus="onFouce"
      :left-icon="imgT"
      :class="{'animation-pause':pause,'animation-move':move}"
      v-if="active!=3"
    >
      <div slot="action" @click="onSearch"></div>
    </van-search>
    <router-view />
  </div>
</template>

<script>
import { Search, Tab, Tabs, Sticky } from "vant";
import axios from "axios";
import img from "../../asset/search.png";
import { setInterval } from "timers";
export default {
  components: {
    [Search.name]: Search,
    [Sticky.name]: Sticky,
    [Tabs.name]: Tabs,
    [Tab.name]: Tab
  },
  data() {
    return {
      active: 0,
      value: "",
      imgT: img,
      pause: false,
      move: false,
      sticky: false
    };
  },
  watch: {
    //监听路由变化
    $route(index) {
      this.active = this.$route.meta.activity;
    }
  },
  mounted() {
    var that = this;
    console.log("appTest", this.VUE_APP_API);
    console.log("img", navigator.userAgent);
    console.log("this", this.$route);
    setInterval(function() {
      var sticky = document.querySelectorAll("#test div");
      document.getElementById("test").style.display = "block";
      document.getElementById("tt").style.display = "block";
      console.log("sticky.length", sticky.length);
      for (let i = 0; i < sticky.length; i++) {
        sticky[i].style.display = "block";
      }
    }, 100);

    // sticky.getElementsByTagName("div").style.display = "none";
    that.active = this.$route.meta.activity;
    axios
      .post(this.VUE_APP_API + "searchRollHotKeyworld.do")
      .then(response => {
        console.log("response", response.data.data);
        that.move = true;
        var i = 0;
        that.interval = setInterval(function() {
          if (i < response.data.data.length) {
            that.value = response.data.data[i];
          } else {
            i = 0;
            that.value = response.data.data[0];
          }
          i++;
        }, 4500);
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  methods: {
    onFouce() {
      if (this.interval) {
        // this.value = "";
        window.clearInterval(this.interval);
        this.pause = true;
        this.$router.push("../search?key=" + this.value);
      }
    },
    onSearch: function() {
      console.log("search");
    }
  }
};
</script>

<style lang="less">
.recommend {
  .headerImg {
    position: relative;
    display: block;
    height: 55.56px;
    .background-img {
      width: 100%;
      height: 55.56px;
    }
    .download-img {
      width: 80px;
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .van-search {
    padding-bottom: 0;
  }
}

#recom-tab .van-tab {
  font-size: 18.75px;
  font-weight: 500;
  color: #585858;
}
.animation-move .van-field__control {
  animation: myfirst 4.5s;
  -webkit-animation: myfirst 4.5s; /* Safari and Chrome */
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite; /*Safari and Chrome*/
}
.animation-pause .van-field__control {
  animation-play-state: paused;
}
.van-search__content {
  .van-cell__value,
  .van-field__body {
    height: 22px;
    input {
      line-height: normal;
    }
  }
}
@keyframes myfirst {
  0% {
    opacity: 0.5;
    padding-top: 0;
    padding-bottom: 20px;
  }
  5% {
    opacity: 1;
    padding-top: 0;
    padding-bottom: 0;
  }
  95% {
    opacity: 1;
    padding-top: 0;
    padding-bottom: 0;
  }
  100% {
    opacity: 0.7;
    padding-top: 20px;
    padding-bottom: 0;
  }
}
</style>
